<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="采购单号">
                            <el-input size="small" v-model="queryData.purchaseNo"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.productList" label="商品目录" template="cascader"
                                           :options="optionList.productLists"
                                           :props="{label: 'category_name', value: 'id'}">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商">
                            <el-input size="small" v-model="queryData.supplier"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="订单状态" template="select"
                                           v-model="queryData.orderState"
                                           :options="optionList.orderStateList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="阿里订单号">
                            <el-input size="small" v-model="queryData.aliOrder"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="职能" template="select"
                                           v-model="queryData.function"
                                           :options="optionList.functionList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员">
                            <el-input size="small" v-model="queryData.opeUser"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.opeTime" label="操作时间" template="daterange">
                        </SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})"
                               icon="icon-chaxun">查询
                    </el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <!--<el-button-->
            <!--v-if="item.method && item.permit(thisVm)"-->
            <!--v-for="item in buttonList"-->
            <!--size="small"-->
            <!--:type="item.type"-->
            <!--:key="item.value"-->
            <!--@click="item.action(thisVm, item.method)">{{item.name}}</el-button>-->
            <el-button type="text" size="small" @click="handleOperateFn(1, '', '确认到样')">
                <i class="icon-querendaoyang icon-main"></i>确认到样
            </el-button>
            <el-button type="text" size="small" @click="handleOperateFn(2, '', '取消买样')">
                <i class="icon-quxiaomaiyang icon-main"></i>取消买样
            </el-button>
            <el-button type="text" size="small" @click="handleOperateFn(3, '', '退回样品')">
                <i class="icon-quxiaomaiyang icon-main"></i>退回样品
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <template slot="sku_list" slot-scope="scope">
                <div>
                    <p v-for="item in scope.row.sku_list">{{`${item.sku} ${item.attribute_value_cn || ''}
                        ${item.purchase_price}`}}</p>
                </div>
            </template>
            <template slot="spu" slot-scope="scope">
                <div>
                    <p>{{scope.row.spu}}</p>
                    <div class="ui-table--icon_btn">
                        <i class="icon-querendaoyang icon-main" @click.stop="handleOperateFn(1, scope.row, '确认到样')"></i>
                        <i class="icon-quxiaomaiyang icon-main" @click.stop="handleOperateFn(2, scope.row, '取消买样')"></i>
                    </div>
                </div>
            </template>
            <template slot="link" slot-scope="scope">
                <div class="upload-img-list" v-if="scope.row.link!==''&&scope.row.link">
                    <div class="ui-com_edit__upload--item">
                        <div class="ui-com_edit__upload--item__img"
                             :style="{background:'url('+scope.row.link+') no-repeat center/contain'}">
                        </div>
                        <div class="ui-com_edit__upload--item--mask">
                            <span class="el-icon-zoom-in" @click="handleLookImage(scope.row.link)"></span>
                        </div>
                    </div>
                </div>
            </template>
            <template slot="product_datum" slot-scope="scope">
                <div>
                    <p><span>购买类型：</span>{{scope.row.sample_type}}</p>
                    <p><span>供应商编码：</span>{{scope.row.supplier_code}}</p>
                    <p><span>供应商名称：</span>{{scope.row.supplier_name}}</p>
                    <p><span>采样链接：</span>{{scope.row.link}}</p>
                    <p><span>联系人/电话：</span>{{scope.row.phone}}</p>
                </div>
            </template>
            <template slot="order_info" slot-scope="scope">
                <div>
                    <p><span>PO：</span>{{scope.row.po}}</p>
                    <p><span>订单号：</span>{{scope.row.supplier_code}}</p>
                </div>
            </template>
            <template slot="logistics_info" slot-scope="scope">
                <div style="cursor: pointer" @click="handleShowLogisticsInfo(scope.row)">
                    <p><span>承运快递：</span>{{scope.row.carrier_name}}</p>
                    <p><span>快递单号：</span>{{scope.row.track_number}}</p>
                    <p><span>最新物流：</span>{{scope.row.new_ship_info}}</p>
                </div>
            </template>
            <template slot="ope_log" slot-scope="scope">
                <div>
                    <p><span>开发人：</span>{{`${scope.row.devp_user} ${scope.row.dv_create_time || ''}`}}</p>
                    <p><span>创建人：</span>{{`${scope.row.create_user} ${scope.row.create_time}`}}</p>
                </div>
            </template>
            <template slot="ope_log" slot-scope="scope">
                <div>
                    <p><span>开发人：</span>{{`${scope.row.devp_user} ${scope.row.dv_create_time || ''}`}}</p>
                    <p><span>创建人：</span>{{`${scope.row.create_user} ${scope.row.create_time}`}}</p>
                </div>
            </template>
            <template slot="track_time" slot-scope="scope">
                <div>
                    <p><span>创建时效：</span>{{scope.row.create_time_slot}}</p>
                    <p><span>付款时效：</span>{{scope.row.pay_time_slot}}</p>
                    <p><span>寄出时效：</span>{{scope.row.send_time_slot}}</p>
                    <p><span>到货时效：</span>{{scope.row.advice_time_slot}}</p>
                    <p><span>跟单时效：</span>{{scope.row.documentary_time_slot}}</p>
                </div>
            </template>
            <template slot="purchase_status" slot-scope="scope">
                <div
                    v-if="scope.row.purchase_status == '待付款'|| scope.row.purchase_status == '待发货'|| scope.row.purchase_status == '待收货'||scope.row.purchase_status == '待退样'">
                    <span class="ui-state_circle state_orange"></span>{{scope.row.purchase_status}}
                </div>
                <div v-if="scope.row.purchase_status == '已收货'"><span class="ui-state_circle state_blue"></span>{{scope.row.purchase_status}}
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <LogisticsInfo :show.sync="isShowLogisticsInfo" :detail="logisticsInfo"></LogisticsInfo>
    </div>
</template>

<script>
import LogisticsInfo from './LogisticsInfo';
import config from '@/assets/js/config';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage
} from '@/services/index';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getCityInfo
} from '@/api/login';
import {
    confirmDownSample,
    backSampleInfo,
    cancelBuySample,
    getLogisticsInfo
} from '@/api/sample_track_manage/sample_track_module/sample_track_manage';

export default {
    components: {
        LogisticsInfo
    },
    data () {
        return {

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                productSPU: '',
                purchaseNo: '',
                productList: [],
                supplier: '',
                orderState: '',
                productName: '',
                aliOrder: '',
                opeUser: '',
                opeTime: '',
                function: '',
            },

            optionList: {
                productLists: [],
                orderStateList: {},
                functionList: {},
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 物流信息
            isShowLogisticsInfo: false,
            logisticsInfo: {}
        };
    },
    computed: {
        ...mapGetters([
            'buttonList',
            'queryParamsInfo',
            'sampleTrackManageInfo'
        ])
    },
    methods: {
        ...mapActions([
            'GetSampleTrackManageInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,
        confirmDownSample,
        cancelBuySample,
        backSampleInfo,

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['sample_track_manage']);
            }

            let obj = {
                spu: this.queryData.productSPU, //商品SPU
                po: this.queryData.purchaseNo, //采购单号
                order_number: this.queryData.aliOrder, //阿里订单号
                category_id: this.queryData.productList, //商品目录
                supplier_code: this.queryData.supplier, //供应商
                sample_type: this.queryData.orderState, //订单状态
                title_cn: this.queryData.productName, //商品名称
                opera_user: this.queryData.opeUser, //操作人员
                opera_time_start: this.queryData.opeTime[0], //操作时间开始
                opera_time_end: this.queryData.opeTime[1], //操作时间结束
                function: this.queryData.function, //职能
                ...params
            };

            this.GetSampleTrackManageInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return;

                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                productSPU: '',
                purchaseNo: '',
                productList: [],
                supplier: '',
                orderState: '',
                productName: '',
                aliOrder: '',
                opeUser: '',
                opeTime: '',
                function: ''
            };
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo({
                isExcel: 1
            }, true);
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 显示物流信息
        handleShowLogisticsInfo (row) {
            getLogisticsInfo({
                po: row.po
            }).then(({data}) => {
                this.logisticsInfo = data.data.data;
                this.isShowLogisticsInfo = true;
            });
        },

        // 确认到样
        // confirmDownSample
        handleOperateFn (type, row, msg) {
            let spu_po_arr = [];
            if (row) {
                spu_po_arr.push({
                    po: row.po,
                    spu: row.spu,
                    return_track_number: row.track_number
                });
            } else {
                if (!this.saveCheckInfo.length) {
                    this.$message.warning('请勾选数据！');
                    return;
                }
                this.saveCheckInfo.forEach(value => {
                    spu_po_arr.push({
                        po: value.po,
                        spu: value.spu,
                        return_track_number: value.track_number
                    });
                });
            }
            this.$confirm(`确认${msg}？`, '提示', {
                type: 'warning'
            }).then(() => {
                let fn = '';
                switch (type) {
                    case 1:
                        fn = 'confirmDownSample';
                        break;
                    case 2:
                        fn = 'cancelBuySample';
                        break;
                    case 3:
                        fn = 'backSampleInfo';
                        break;
                }
                this[fn]({
                    spu_po_arr: JSON.stringify(spu_po_arr)
                }).then(({data}) => {
                    this.handleOperationCallBack(data);
                });
            });
        },

        // 操作回调
        handleOperationCallBack (data) {
            if (data.status) {
                this.$message.success('操作成功');
                this.handleQueryInfo({limit: this.pageData.currentList});
            } else {
                this.$message.error(data.errorMess || '操作失败');
            }
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'sample_track_manage',
                value: JSON.stringify(this.queryData)
            });

            // 渲染下拉
            this.optionList.productLists = data.drop_down_box.category_list.data;
            this.optionList.orderStateList = data.drop_down_box.sample_order_type;
            this.optionList.functionList = data.drop_down_box.function;

            //数据渲染
            this.tableData = data.data.data_list.value;
            this.tableKey = [...data.data.data_list.key];
            this.tableKey.unshift('序号');

            // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
            this.tableProps = ['index', 'spu', 'sku_list', 'link', 'title_cn', 'category_path', 'product_datum', 'order_info', 'logistics_info', 'ope_log', 'track_time', 'purchase_status'];
            this.tableWidth = ['60', '100', '300', '70', '250', '150', '300', '160', '200', '220', '220', '80'];

            //渲染分页
            this.pageData.currentList = data.data.paging_data.limit * 1;
            this.pageData.currentPage = data.data.paging_data.offset * 1;
            this.pageData.currentTotal = data.data.paging_data.total * 1;
        }
    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.sampleTrackManageInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['sample_track_manage']);
                this.handleQueryInfoCallBack(this.sampleTrackManageInfo);
            }
        } catch (e) {
            console.log(e);
        }
    },
};
</script>

<style lang="less" scoped>

.ui-table--supplier_code {
    font-size: 14px;
    font-weight: bold;
    line-height: 14px;

    p {
        margin-bottom: 10px;
    }
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: 185px;
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 185px;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}

.ui-table__img img {
    max-width: 70px;
    max-height: 90px;
}
</style>



